<template>
  <v-app-bar id="topo" clipped-left app color="primary" dark>
    <v-btn text @click="toggleMenu()" class="d-md-none pl-0">
      <v-icon>
        menu
      </v-icon>
    </v-btn>

    <v-toolbar-title class="ml-sm-4">
      {{ title }}
      <small v-if="subtitle" class="d-none d-md-inline barbershopname ml-2">
        {{ subtitle }}
      </small>
    </v-toolbar-title>
    <v-spacer></v-spacer>

    <v-menu class="btn-avatar" transition="slide-y-transition" bottom>
      <template v-slot:activator="{ on }">
        <div class="btn-avatar" dark v-on="on">
          <span class="mr-2 d-none d-md-inline">
            {{ name }}
          </span>

          <div class="d-none d-md-inline">
            <v-avatar class="gray2" size="48px" item>
              <v-img v-if="avatar" :src="avatarComputed"> </v-img>
            </v-avatar>
          </div>

          <v-icon class="ml-2 d-none d-md-inline">
            keyboard_arrow_down
          </v-icon>

          <v-icon class="ml-2 d-inline d-sm-inline d-md-none">
            more_vert
          </v-icon>
        </div>
      </template>
      <v-list>
        <v-list-item
          v-for="(item, i) in items"
          :key="i"
          @click="clickMenu(item)"
        >
          <v-list-item-title>
            {{ item.title }}
          </v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
  </v-app-bar>
</template>

<script>
export default {
  name: 'Topo',
  props: {
    avatar: { default: null },
    items: { default: Array },
    name: { default: null },
    title: { default: 'Meu Barbeiro' },
    subtitle: { default: null },
  },
  computed: {
    avatarComputed () {
      return this.avatar ? `${this.$url_backend}${this.avatar}` : null;
    }
  },
  methods: {
    toggleMenu() {
      this.$emit('toggleMenu');
    },
    clickMenu(item) {
      this.$emit('clicked', item);
    },
  },
};
</script>

<style lang="scss" scoped>
#topo {
  .btn-avatar { cursor: pointer; }
  .barbershopname { font-size: 12px; }
  .notification-icon { cursor: pointer; }
}
</style>
